import React from 'react';
import styles from './AppSettings.module.css'

const AppSettings = (props) => {
    return (

        <div className={styles.containerAppSettings}>
            <div className='FillContainer'>
                <button >Team Settings</button>
                <button className='Selected'>App Settings</button>
            </div>

            <div >
                <input type='checkbox' checked='true'></input>
                <label>Include character promo</label>
                <br></br>
                <input type='checkbox' checked='true'></input>
                <label>Include promo items</label>
         
                <br></br>
                <input type='checkbox' checked='true'></input>
                <label>Include custom characters</label>
     
                <br></br>
                <input type='checkbox' checked='true'></input>
                <label>use gray-scale sceme</label>
         
            </div>

            <div className="center">
                <button>Add Custom characters</button>
                <br></br>
                To add a list here
            </div>

            <div className='FillContainer'>

                <button className="EvenMargin">  Credits</button>

                <button className="EvenMargin">Edit Decks</button>
            </div>
          
        </div>)
}

export default AppSettings;